.dialogs {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
}

.dialogsBlock {
    display: flex;
    justify-content: space-between;
    width: 90%;

}

.dialogItems {
    padding: 10px;
    width: 210px;
}

.dialogItem {
    color: #000000;
    text-decoration: none;
    padding: 10px;
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 10px;
}

.dialogItem img {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid #a94fc2;
}

.dialogItemLink {
    padding: 10px;

}

.messageItems {
    padding: 5px;
}

.messageItem {
    width: 300px;
    color: #525252;
    padding: 10px;
    background-color: #e2e1e7;
    border-radius: 5px;
    margin: 10px;
}


.addMessageForm {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 90%;
    justify-content: space-between;
    padding: 10px;
}

.field {
    width: 400px;
}


